import {getDcotorByName} from '@/common/api/doctor.js'
import {error} from '@/common/js/errorTips.js'

export default {
	data() {
		return {
			inputVal: "",
			isActived: 0,
			iconURL: '/static/appointment/',
			doctorRecordList: [
			{
				id: 10009,
				name: '王晗'
			 }//,{
			// 	id: 3,
			// 	name: 'test1'
			// },{
			// 	id: 4,
			// 	name: 'test1'
			// },{
			// 	id: 5,
			// 	name: 'test1'
			// },{
			// 	id: 6,
			// 	name: 'test1'
			// },{
			// 	id: 7,
			// 	name: 'test1'
			// },{
			// 	id: 10,
			// 	name: 'test1'
			// },
			],
			doctorList: []
		}
	},
	methods:{
		// 清除输入框的东西
		clearInput:function(){
			this.inputVal = ''
		},
		// 获取医生信息
		getDoctorList: function() {
			this.doctorList = [];
			uni.showLoading({
				title: '加载中'
			})
			getDcotorByName(1, 50, this.inputVal.trim()).then(res => {
				if(res.data.code === 200) {
					const data = res.data.data.list
					if(data.length > 0) {
						this.doctorList = data
						uni.hideLoading()
					} else {
						uni.hideLoading()
						error('没有该医生信息，请确认医生姓名')
					}
				}
			}).catch(() => {
				uni.hideLoading()
				error('网络')
			})
		},
		// 跳转到医生的简介及出诊时间详情
		toDoctorBrief:function(doctorInfo){
			uni.setStorageSync('doctorInfo', JSON.stringify(doctorInfo))
			uni.navigateTo({
				url: '/pagesB/pages/appointPages/doctorAppointDetail' + 
				'/doctorAppointDetail?isTreatmentTime=' + 2 
			})
		},
		// 切换挂号记录中的医生
		changeDoctor:function(e){
			this.isActived = e.currentTarget.dataset.name
			uni.navigateTo({
				url: '/pagesB/pages/appointPages/doctorAppointDetail/doctorAppointDetail?isTreatmentTime=' + 2
			})
			var that = this
			setTimeout(function(){
				that.isActived = 0
			},2000)
		}
	}
}
